// import uniIcon from "../../../components/uni-icon/uni-icon.vue"
import {
	uniIcon
} from '@dcloudio/uni-ui/lib/uni-icon/uni-icon.vue'
export default {
	data() {
		return {
			active1: 1,
			active2: 1,
			active3: 1,
			show: false,
			checkAll: false,
			list: ['a', 'b', 'c'],
			result: ['a', 'b'],
			thesaurusLists: [],
			chapterLists: []

		};
	},
	components: {
		uniIcon
	},
	methods: {
		actives(val) {
			console.log(val)
			this.val = !this.val
			this.show = !this.show
			console.log(this.val)
		},
		actives1() {
			this.active1 = !this.active1
			this.show = !this.show
			console.log(this.active1)
		},
		actives2() {
			this.active2 = !this.active2
			this.show = !this.show
			console.log(this.active2)
		},
		actives3() {
			this.active3 = !this.active3
			this.show = !this.show
			console.log(this.active3)
		},
		optionsActive(val) {
			console.log(val)
			this.show = false
			this.active1 = !this.active1
		},
		onChange(event) {
			console.log(event.detail)
			this.checkAll = event.detail
		},
		onChanges(event) {
			this.result = event.detail
			console.log(this.result)
		},
		// 获取课程下拉列表
		thesaurusList() {
			uni.request({
				url: 'http://192.168.2.107:8089/backwordSystem/teacher/task/thesaurusList',
				data: {},
				success: (data) => {
					console.log(data)
					this.thesaurusLists = data.data
				}
			})
		},
		// 获取章节下拉列表
		chapterList() {
			uni.request({
				url: 'http://192.168.2.107:8089/backwordSystem/teacher/task/chapterList',
				data: {},
				success: (data) => {
					console.log(data)
					this.chapterLists = data.data
				}
			})
		},
	},
	created() {
		this.thesaurusList()
	}
}
